html,
body {
    background: $bg;
    color: $col;
    // font-family: $font-stack;
    margin: 0;
    padding: 0;
    font-size: 16px;


    font-family: 'Roboto', sans-serif;


    ul {
        li {
            list-style: none;
        }

    }

    a {

        color: $col_link;
    }
}


h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
div,
label {

    box-sizing: border-box;
    color: inherit;
}
.section{
  padding: 1rem 0;
  p:last-child{
    margin-bottom: 0;
  }
}
.container {
    width: 1200px;
    padding: 35px;
    margin: 0 auto;
}

h3 {
    font-size: 1.5rem;
    color: $heading;
    margin-top: 0;
}


#jet-header {
    .container {
        display: flex;
        justify-content: space-between;
        padding: 10px;
    }
}


#jet-footer {
    background: $footer;
    padding: 30px 0;
    color: inherit;

    .container {
        color: inherit;
        height: 116px;
        display: flex;
        justify-content: center;
        align-items: center;
    }




    // .abc-logo{
    //     img{
    //         // width: ;
    //     }
    // }
}

.subfooter {
    padding: 10px 30px;
    color: inherit;

    ul {
        display: flex;
        padding: 0;
        margin: 0;
        justify-content: center;
        gap: 30px;

        li {
            display: flex;

            img {
                width: 150px;
            }

            align-items: center;
        }

    }
}

.copyright {
    color: inherit;
    display: flex;
    justify-content: center;
    font-size: 12px;
    text-align: center;
}
button{
    transition: all ease 0.3s;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1.1rem;
    letter-spacing: 0.5px;

    &:hover{
        background: $but-hover!important;
        color: $but-hover-col!important;
    }
}

.g-1{
    display: flex;
    gap: 1rem;
    .w-3-5{
        width: 60%;
    }
    .w-2-5{
        width: 40%;
    }
}